import React, {Component} from "react";
import {DatePicker} from "../../../core/components/ui/Datepicker";
import Grantor from "./Grantor";
import OnlineUtils from "eshop/utils/OnlineUtils";
import ErrorRow from "../../../core/components/hoc/ErrorRow";

export default class Grantors extends Component {
    constructor(props) {
        super(props);
    }

    removeFactory() {
        if(this.props.minCount && this.props.data.length > this.props.minCount) {
            return this.props.remove;
        }
    }

    onDateChange(event) {
        this.props.setGrantingDate(event.value)
    }

    getDateProps() {
        return {
            id: "granting-date",
            floatingLabel: "Data udzielenia pe≈Çnomocnictwa",
            onChange: this.onDateChange.bind(this),
            value: this.props.grantingDate,
            options: {
                disabledWeekDays: [],
                maxDate: OnlineUtils.toDateStr(new Date())
            }
        };
    }

    render() {
        return !!this.props.data.length && (
            <div>
                <div className="h4">{ this.props.data.length > 1 ? this.props.titlePlural: this.props.title}</div>
                <ErrorRow errors={this.props.grantingDateErrors}>
                    <DatePicker {...this.getDateProps()}/>
                </ErrorRow>
                {
                    this.props.data.map((data, index) =>
                        <Grantor {...data} key={index} index={index} onChange={this.props.onChange} remove={this.removeFactory()} useLabel={this.props.data.length > 1}/>
                    )
                }
            </div>
        )
    }
}